import React from 'react';
import Stopwatch from './Stopwatch';
import Assessment from './Assessment';
import timer from '../timer';
import Title from '../Title';

let OneSecondIncrementingTimer = timer(1000)(Stopwatch);

const Test = ({
  updatedButtonChoice,
  testProtocol,
  walkingPace,
  verbalAssistance,
  assistiveDevice,
  addTest,
  count
}) => {
  return (
    <div>
      <Title title="Assessment" />
      <div className="test-container">
        <OneSecondIncrementingTimer addTest={addTest} count={count} />
        <Assessment
          updatedButtonChoice={updatedButtonChoice}
          testProtocol={testProtocol}
          walkingPace={walkingPace}
          verbalAssistance={verbalAssistance}
          assistiveDevice={assistiveDevice}
          count={count}
        />
      </div>
    </div>
  );
};

export default Test;



