import React, { Component } from 'react';
import Test from '../components/Test/Test';

class TestContainer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      testProtocol: 'Static',
      walkingPace: 'Usual',
      verbalAssistance: 'No',
      assistiveDevice: 'None',
      isRunning: false,
      value: 0,
      id: Date.now()
    };
  }

  componentDidMount = () => {
    const {
      testProtocol,
      walkingPace,
      verbalAssistance,
      assistiveDevice
    } = this.state;
    this.props.updatedTestChoices(
      testProtocol,
      walkingPace,
      verbalAssistance,
      assistiveDevice
    );
  };

  updatedButtonChoice = (slug, newValue) => {
    this.setState({ [slug]: newValue }, () => {
      const {
        testProtocol,
        walkingPace,
        verbalAssistance,
        assistiveDevice
      } = this.state;
      this.props.updatedTestChoices(
        testProtocol,
        walkingPace,
        verbalAssistance,
        assistiveDevice
      );
    });
  };

  render() {
    return (
      <div>
        <Test
          {...this.state}
          updatedButtonChoice={this.updatedButtonChoice}
          addTest={this.props.addTest}
          count={this.props.count}
        />
      </div>
    );
  }
}

export default TestContainer;



