import React from 'react';

const label = {
  top: {
    label1: 'Result',
    label2: 'Baseline',
    label3: '%Improved'
  },
  side: {
    label1: 'Average Speed ( m / s )',
    label2: 'Max Speed ( m / s )',
    label3: 'Total Time ( seconds )'
  }
};

const TopLabel = props => {
  return (
    <div className="result-row labels">
      <div className="result-label" />
      <div className="result-item">
        {label.top.label1}
      </div>
      <div className="result-item">
        {label.top.label2}
      </div>
      <div className="result-item">
        {label.top.label3}
      </div>
    </div>
  );
};

const Result = props => {
  return (
    <div className="result-row">
      <div className="result-label">
        {props.label}
      </div>
      <div className="result-item">
        {props.result}
      </div>
      <div className="result-item">--</div>
      <div className="result-item">--</div>
    </div>
  );
};

const Table = ({ averageSpeed, totalTime }) => {
  return (
    <div className="result-table">
      <TopLabel />
      <Result label="Average Speed (m/s)" result={averageSpeed} />
      <Result label="Total Time ( seconds )" result={totalTime} />
    </div>
  );
};

export default Table;



