import React from 'react';

const TestInfo = ({ title, description }) => {
  return (
    <li className="nav-test">
      <div className="nav-test-title">
        <span>
          {title}
        </span>
        <p>
          {description}
        </p>
      </div>
    </li>
  );
};

export default TestInfo;



