import React from 'react';
import PropTypes from 'prop-types';

const label = {
  label1: 'N.',
  label2: 'Average Speed (m / s)',
  // label3: 'Max Speed (m / s)',
  label3: 'Total Time (seconds)'
};

const Label = ({ label, number }) => {
  return (
    <div className={!number ? 'labelbar-container' : 'labelbar-container num'}>
      {label}
    </div>
  );
};

Label.propTypes = {
  label: PropTypes.string.isRequired
};

const LabelBar = props => {
  return (
    <div className="icon-bar label-bar">
      <Label label={label.label1} number={true} />
      <Label label={label.label2} />
      <Label label={label.label3} />
    </div>
  );
};

export default LabelBar;



