import React from 'react';
import PropTypes from 'prop-types';

const Circle = props => {
  return (
    <div className="circle">
      {props.label}
    </div>
  );
};

Circle.propTypes = {
  label: PropTypes.number.isRequired
};

const CheckedCircle = () => {
  let checkmark = <i className="material-icons">done</i>;
  return (
    <div className="circle checkmark">
      {checkmark}
    </div>
  );
};

const Count = ({ count }) => {
  return (
    <div className="count-container">
      {count < 1 ? <Circle label={1} /> : <CheckedCircle />}
      <div className="circle-line" />
      {count < 2 ? <Circle label={2} /> : <CheckedCircle />}
      <div className="circle-line" />
      {count < 3 ? <Circle label={3} /> : <CheckedCircle />}
    </div>
  );
};

export default Count;



