import React from 'react';

const Timer = ({ timer }) => {
  return (
    <div className="stopwatch-item timer">
      <div className="test-label">Timer (sec)</div>
      <div className="test-stat">
        {timer.tick}
      </div>
    </div>
  );
};

const StopwatchButton = ({ timer, addTest, avgSpeed, count }) => {
  var play = <i className="material-icons">play_arrow</i>;
  var stop = <i className="material-icons">stop</i>;

  function handleStop() {
    timer.stop();
    addTest({ value: timer.tick, id: Date.now(), averageSpeed: avgSpeed });
  }

  let button;

  if (count === 3) {
    button = (
      <button className="stopwatch-button complete">
        Tests Completed<i className="material-icons">done</i>
      </button>
    );
  } else if (!timer.tick && count < 3) {
    button = (
      <button className="stopwatch-button play" onClick={timer.resume}>
        Start Timer {play}
      </button>
    );
  } else {
    button = (
      <button className="stopwatch-button stop" onClick={handleStop}>
        Stop Timer {stop}
      </button>
    );
  }

  return (
    <div className="stopwatch-item">
      {button}
    </div>
  );
};

const StopwatchStat = ({ timer, avgSpeed }) => {
  return (
    <div className="stopwatch-item stat">
      <div className="test-label">Avg Speed ( m / sec )</div>
      <div className="test-stat">
        {timer.tick > 0 ? avgSpeed : 0}
      </div>
    </div>
  );
};

const Stopwatch = ({ timer, isRunning, addTest, count }) => {
  let avgSpeed = Math.round(10 / timer.tick * 100) / 100;

  return (
    <div className="stopwatch-container">
      <Timer timer={timer} />
      <StopwatchButton
        addTest={addTest}
        avgSpeed={avgSpeed}
        timer={timer}
        count={count}
      />
      <StopwatchStat avgSpeed={avgSpeed} timer={timer} />
    </div>
  );
};

export default Stopwatch;



