import React from 'react';
import ResultsTable from './ResultsTable';
import TextField from '../Test/TextField';
import IconBar from '../IconBar/IconBar';
import Title from '../Title';

const TestResults = props => {
  return (
    <div className="result-footer">
      <Title title="Test Results" />
      <div className="test-results-container">
        <ResultsTable
          averageSpeed={props.averageSpeed}
          totalTime={props.totalTime}
        />
        <TextField
          label="Test Result Statement"
          testProtocol={props.testProtocol}
          walkingPace={props.walkingPace}
          verbalAssistance={props.verbalAssistance}
          assistiveDevice={props.assistiveDevice}
        />
      </div>
      <IconBar />
    </div>
  );
};

export default TestResults;



