import React, { Component } from 'react';
import Nav from '../components/Nav/Nav';

class NavContainer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      patient: {
        name: 'Jane',
        lastName: 'Doe',
        dob: '08/12/1989',
        sex: 'Female'
      }
    };
  }

  render() {
    return (
      <div>
        <Nav patient={this.state.patient} />
      </div>
    );
  }
}

export default NavContainer;



