import React, { Component } from 'react';
import NavContainer from './containers/NavContainer';
import CountContainer from './containers/CountContainer';
import TestContainer from './containers/TestContainer';
import IconBarContainer from './containers/IconBarContainer';
import TestResultsContainer from './containers/TestResultsContainer';
import IndividualResult from './components/IndividualResult/IndividualResult';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      tests: [],
      averageSpeed: 0,
      totalTime: 0,
      count: 0
    };
  }

  addTest = test => {
    let tests = [...this.state.tests];
    let count = this.state.count;

    if (tests.length < 3) {
      tests.push(test);
      count++;
      this.setState({ tests: tests, count: count }, () => {
        this.calculatedTestResults();
      });
    } else {
      alert('Already completed!');
    }
  };

  calculatedTestResults = () => {
    let speeds = [];
    let times = [];
    let tests = [...this.state.tests];
    let averageSpeed = this.state.averageSpeed;
    let totalTime = this.state.totalTime;

    if (tests.length === 3) {
      for (let test of tests) {
        speeds.push(test.averageSpeed);
        times.push(test.value);
      }
      averageSpeed =
        speeds.reduce((previous, current) => (current += previous)) / 3;
      averageSpeed = Math.round(averageSpeed * 100) / 100;
      totalTime = times.reduce((previous, current) => (current += previous));
      this.setState(
        { averageSpeed: averageSpeed, totalTime: totalTime },
        () => {
          console.log(this.state);
        }
      );
    }
  };

  deleteTest = testId => {
    let count = this.state.count;
    count--;
    let filteredTests = this.state.tests.filter(test => {
      return test.id !== testId;
    });
    this.setState({
      tests: filteredTests,
      averageSpeed: 0,
      totalTime: 0,
      count: count
    });
  };

  editTest = testId => {
    // Do state
  };

  updatedTestChoices = (
    testProtocol,
    walkingPace,
    verbalAssistance,
    assistiveDevice
  ) => {
    this.setState({
      testProtocol: testProtocol,
      walkingPace: walkingPace,
      verbalAssistance: verbalAssistance,
      assistiveDevice: assistiveDevice
    });
  };

  render() {
    return (
      <div>
        <NavContainer />
        <CountContainer count={this.state.count} />
        <TestContainer
          addTest={this.addTest}
          numberOfTests={this.state.tests.length}
          count={this.state.count}
          updatedTestChoices={this.updatedTestChoices}
        />
        <IconBarContainer />
        <IndividualResult
          tests={this.state.tests}
          calculatedTestResults={this.calculatedTestResults}
          deleteTest={this.deleteTest}
          editTest={this.editTest}
        />
        <TestResultsContainer
          tests={this.state.tests}
          averageSpeed={this.state.averageSpeed}
          totalTime={this.state.totalTime}
          testProtocol={this.state.testProtocol}
          walkingPace={this.state.walkingPace}
          verbalAssistance={this.state.verbalAssistance}
          assistiveDevice={this.state.assistiveDevice}
        />
      </div>
    );
  }
}

export default App;



